import { Component, OnInit, Input, OnChanges } from '@angular/core';

import { CacheService } from '../../../services/cache.service';

@Component({
  selector: 'app-side-info',
  templateUrl: './side-info.component.html',
  styleUrls: ['./side-info.component.css']
})
export class SideInfoComponent implements OnInit, OnChanges {
  @Input('contentIndex') index:string;
  activeTitle:string;
  activeDesc:string;
  activeImages:string[];
  
  constructor(private cacheService:CacheService) { }
  
  handleContent() {
    const current = this.cacheService.getProjects().get(this.index);
    
    this.activeTitle = current.title;
    this.activeDesc = current.desc;
    this.activeImages = current.icons;
  }

  ngOnInit() {
    this.handleContent();
  }
  
  ngOnChanges() {
    this.handleContent();
  }
}
