import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Observable, Subject } from 'rxjs/Rx';

import { Article } from '../article';
import { CacheService } from '../../../services/cache.service';
import { HttpService } from '../../../services/http.service';
import { WebSocketService } from '../../../services/web-socket.service';

@Component({
  selector: 'app-article',
  templateUrl: './article.component.html',
  styleUrls: ['./article.component.css']
})
export class ArticleComponent implements OnInit {
  article:Article;
  comments:object[];
  msgs:object[] = [];
  
  constructor(
    private route:ActivatedRoute,
    private cacheService:CacheService,
    private http:HttpService,
    private location:Location,
    private webSocket:WebSocketService) { }
    
  goBack() {
    this.location.back();
  }
  
  handleGrowl(message) {
    this.msgs.push({ severity: 'error', summary: 'Failure...', detail: message });
  }
  
  handlePurr(message) {
    this.msgs.push({ severity: 'info', summary: 'Success!', detail: message });
  }
  
  handleSubmit(event) {
    event.preventDefault();
    
    const name = event.target.querySelector('#blog-form-name').value;
    const comment = event.target.querySelector('#blog-form-comment').value;
    
    if (!name && !comment) {
      this.handleGrowl('Nothing has been entered!');
    }
    else if (!name) {
      this.handleGrowl('You forgot to enter your name!');
    }
    else if (!comment) {
      this.handleGrowl('You missed typing out a comment!');
    }
    else {
      this.http.postCommentToArticle(name, comment, this.article.id)
        .subscribe((comment) => {
          if (comment) {
            event.target.querySelector('#blog-form-name').value = '';
            event.target.querySelector('#blog-form-comment').value = '';
            
            this.handlePurr('Thanks for the feedback!');
            this.sendComment(comment);
          }
          else {
            this.handleGrowl('Submission failed. Please try again later!');
          }
        });
    }
  }
  
  sendComment(comment) {
    this.webSocket.getSubject().next(comment);
  }

  ngOnInit() {
    this.article = <Article>this.cacheService.getArticle(this.route.snapshot.paramMap.get('id'));
    
    this.webSocket.getSubject()
      .subscribe(comment => this.comments = this.comments ? [...this.comments, comment] : [comment]);
    
    this.article && this.http.getCommentsForArticle(this.article.id)
      .subscribe(comments => comments ? this.comments = [...comments] : null);
  }
}