import { Component, Output, EventEmitter } from '@angular/core';

import { HttpService } from '../../../services/http.service';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent {
  @Output() growl = new EventEmitter();
  @Output() purr = new EventEmitter();
  
  constructor(private http:HttpService) { }
  
  handleSubmit(event) {
    event.preventDefault();
    
    const name = event.target.querySelector('#home-form-name').value;
    const message = event.target.querySelector('#home-form-message').value;
    
    if (!name && !message) {
      this.growl.emit('Nothing has been entered!');
    }
    else if (!name) {
      this.growl.emit('You forgot to enter your name!');
    }
    else if (!message) {
      this.growl.emit('You missed typing out a message!');
    }
    else {
      this.http.postMessage(name, message)
        .subscribe((success) => {
          if (success) {
            event.target.querySelector('#home-form-name').value = '';
            event.target.querySelector('#home-form-message').value = '';
            
            this.purr.emit('Message submitted. Thank you!');
          }
          else {
            this.growl.emit('Submission failed. Please try again later!');
          }
        });
    }
  }
}
