import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of, pipe } from 'rxjs';
import { catchError, tap, map } from 'rxjs/operators';

import config from '../app.config.js';
import { LoggerService } from './logger.service';

@Injectable({
  providedIn: 'root'
})
export class HttpService {
  serverURL:string = config.serverURL;

  constructor(
    private logger:LoggerService,
    private http:HttpClient) { }

  private log(message) {
    this.logger.add(message);
  }

  private handleError<T>(operation = 'operation', result?:T) {
    return (error):Observable<any> => {
      this.log(`${operation} failed: ${error}`);
      return of(<T>result);
    }
  }

  getCommentsForArticle(articleId):Observable<any> {
    return this.http.get(`${this.serverURL}/allComments?id=${articleId}`)
      .pipe(
        tap(() => this.log(`Getting all comments concerning: ${articleId}`)),
        catchError(this.handleError<any>('getCommentsForArticle', articleId))
      );
  }

  postCommentToArticle(name, comment, articleId):Observable<any> {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json' })
    };

    return this.http.post(`${this.serverURL}/newComment`, { name, comment, postedTo: articleId }, httpOptions)
      .pipe(
        tap(() => this.log(`Posting new comment submitted by: ${name}`)),
        catchError(this.handleError<any>('postCommentToArticle', comment))
      );
  }

  postMessage(name, message):Observable<any> {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json' })
    };

    return this.http.post(`${this.serverURL}/newMessage`, { name, message }, httpOptions)
      .pipe(
        tap(() => this.log(`Posting new messaged submitted by: ${name}`)),
        catchError(this.handleError<any>('postMessage', message))
      );
  }
}
