import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LoggerService {
  history:string[] = [];
  
  add(message:string) {
    this.history.push(message);
    this.head();
  }
  
  head() {
    console.log(this.history[this.history.length - 1] || '...');
  }
  
  clear() {
    this.history = [];
  }
}
